/* Universal Styles */

html {
	font-size: 18px;
}

@media only screen and (max-width: 560px) {
	html {
		font-size: 15px;
	}
}

input:focus {
	outline: none;
}

/* Reusable Component - Image Container */

.image-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	overflow: hidden;
	background: #000;
}

.image-container img {
	width: 100%;
}

/* App Styles */

.App {
	font-family: 'Work Sans', sans-serif;
	text-align: center;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}

h1 {
	padding: 0.66rem 0;
	text-align: center;
	background-color: #cca353;
	font-family: Poppins, sans-serif;
	font-size: 2rem;
	color: #fff;
}

.App-logo {
	animation: App-logo-spin infinite 20s linear;
	height: 80px;
}

.App-header {
	background-color: #222;
	height: 150px;
	padding: 20px;
	color: white;
}

.App-title {
	font-size: 1.5em;
}

.App-intro {
	font-size: large;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
